import React from "react";

export default function Resume() {
  return (
    <section
      id="resume"
      className="scroll-mt-24 py-12 border-b border-gray-200 dark:border-gray-800"
    >
      <h2 className="text-3xl font-bold mb-4">Resume</h2>
      <p className="text-lg text-gray-600 dark:text-gray-300">
        Include your resume or a link to download it here.
      </p>
    </section>
  );
}
